@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap');

.app{
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main{
  width: 76rem;
  height: 44.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  color: #000000;
  font-family: "Kalam", cursive;
  font-size: 4rem;
}

p{
  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: 3rem;
}
p em{
  font-weight: bold;
  color: #FFF1FA;
}

.boardTitle{
  color: #000000;
  font-family: "Kalam", cursive;
  font-size: 4rem;

  width: 33rem;
  height: 4rem;
  
  background: #99714a;

  border-style: solid solid none solid;
  border-color:#99714a;
  border-width: 1.5rem;
  border-radius: 1rem 1rem 0rem 0rem;

  line-height: 4rem;
  text-align: center;
}
.board{
  display: grid;
  grid-template-columns: repeat(4, 16rem);
  grid-auto-rows: 20rem;
  gap: 1rem;
  background-color: #C4A484;
  padding: 3rem;

  border-style: solid;
  border-color:#99714a;
  border-width: 1.5rem;
  border-radius: 1rem;
}
img{
  width: 100%;
  height: 100%;
}


img:nth-child(2n) {transform: rotate(-2deg); }
img:nth-child(3n) {transform: rotate(6deg); }
img:nth-child(4n) {transform: rotate(-8deg); }
img:nth-child(1n) {transform: rotate(8deg); }
img:nth-child(5n) {transform: rotate(-6deg); }
img:nth-child(6n) {transform: rotate(10deg); }
img:nth-child(7n) {transform: rotate(8deg); }
img:nth-child(8n) {transform: rotate(-8deg); }
.body{
  border: none;
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(3, calc(32rem/3));
  grid-template-rows: 5rem 14rem;
  gap: 0.5rem;
}
.timeTitle{
  grid-column: span 3;

  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: 3rem;

  text-align: center;
}
.timeUnit{
  background: white;

  border-style: solid;
  border-color: white;
  border-width: 0.5rem;
  border-radius: 0.4rem;

  text-align: center;
  vertical-align: middle;

  font-family: "Kalam", cursive;
  line-height: 4rem;
  font-size: 1.8rem;
}
.timeNum{
  border: none;
  background: lightgray;
  width: calc(29rem/3);
  height: calc(29rem/3);
  line-height: calc(29rem/3);

  border-color: lightgray;
  border-radius: 0.2rem;
  
  font-size: 5rem;
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}